:root {
  --text: black;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  background: black;
  border-top: 1px solid red;
  padding: 20px 0;
  width: 100%;
}

.header img {
  display: block;
  margin: 0 auto;
  width: 250px;
}

.login-form {
  margin: 200px auto 0 auto;
  width: 500px;
}

.content-form {
  margin: 50px auto;
  width: 800px;
}

.news-update-form {
  width: 600px;
}

fieldset {
  margin: 0 0 10px 0;
  padding: 20px 20px 10px 20px;
}

legend {
  padding: 0 5px;
}

input {
  height: 40px;
  margin: 0 0 20px 0;
  padding: 0 10px;
  width: 100%;
}

.social-media > div {
  width: 200px;
  padding: 10px;
  
}

.social-media div:first-of-type {
  width: auto;
  padding: 0;
}

.social-media p {
  margin-bottom: 10px;
}

.social-media input {
  display: inline-block;
  height: auto;
  margin: 0 10px 0 -10px;
  width: auto;
}

.social-media label {
  text-align: left;
  display: inline-block;
}

.image-upload {
  border-bottom: 1px solid gray;
  display: flex;
}

.image-upload label {
  width: 115px;
}

.image-upload input {
  margin: 0;
}

.video-upload {
  display: flex;
  margin-top: 20px;
}

.video-upload label {
  width: 210px;
}



button {
  height: 40px;
  width: 100%;
}

textarea {
  height: 200px;
  margin: 0 0 20px 0;
  padding: 10px;
  width: 100%;
}



/**
 * Modal
 */
.modal {
  background: rgba(0, 0, 0, 0.6);
  display: none;
  height: 100%;
  position: absolute;
  width: 100%;
  z-index: 9;
}

.modal > div {
  background: white;
  border-radius: 2px;
  left: 50%;
  padding: 20px 40px 60px 40px;
  position: absolute;
  top: 10%;
  transform: translateX(-40%);
}

.close-btn {
  background: none;
  border: 0;
  color: #222;
  cursor: pointer;
  float: right;
  font-size: 25px;
}

.close-btn:hover {
  color: gray;
}


/**
 * Sections
 */
.sections {
  display: flex;
  flex-flow: row wrap;
  width: 1280px;
  margin: 0 auto;
}


/**
 * News
 */

.article {
  width: 400px;
  margin: 40px 40px 40px 0;
  background: white;
}
.article:nth-of-type(3n) {
  margin-right: 0;
}
.article figure {
  cursor: pointer;
  border: 1px solid gray;
  width: 100%;
  height: 200px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  background: black;
}
.article img {
  width: 100%;
}
.article figcaption {
  width: 100%;
  color: #eee;
  background: rgba(0,0,0,0.75);
  padding: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
}
.article h2 {
  font-size: 24px;
  font-weight: lighter;
  margin: 20px 0 5px 0;
}
.article address, .article time {
  display: inline-block;
  margin-bottom: 20px;
  font-size: 14px;
}
.article p {
  font-size: 16px;
  line-height: 1.2;
  width: 100%;
}
.fold {
  display: none;
}
.show-fold {
  display: block;
}
.toggle-fold {
  display: inline;
  cursor: pointer;
  font-size: 14px;
  white-space: nowrap;
}
.article a,
.article a:link,
.article a:visited,
.article a:active {
  color: red;
  text-decoration: none;
}
.article a:hover {
  color: maroon;
  text-decoration: underline;
}



.admin-btn-container {
  width: 1000px;
  margin: 40px auto 0 auto;
  position: relative;
  height: 41px;
}

.update-btn {
  position: absolute;
  right: 55px;
  width: 50px;
}

.delete-btn {
  position: absolute;
  right: 0;
  width: 50px;
}

.individual-article {
  max-width: 1000px;
  margin: 5px auto 40px auto;
  background: white;
}

.individual-article figure {
  cursor: pointer;
  background: gray;
  border: 1px solid gray;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.individual-article img {
  width: 100%;

  min-height: 200px;
}

.individual-article figcaption {
  width: 100%;
  color: #eee;
  background: rgba(0, 0, 0, 0.75);
  font-size: 18px;
  padding: 5px 10px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.individual-article h2 {
  font-size: 40px;
  font-weight: lighter;
  margin: 20px 0;
}

.individual-article address,
.individual-article time {
  display: inline-block;
  margin-bottom: 20px;
  font-size: 18px;
}

.individual-article p {
  font-size: 26px;
  line-height: 1.2;
  width: 100%;
}

.individual-article a {
  color: red;
  text-decoration: none;
}

.individual-article a:hover {
  color: maroon;
}
